import { contribuildApi } from "../instance";

export const REGISTER_USER = async (data: {
    first_name : string,
    last_name : string,
    phone : string,
    email : string,
    password : string
}) => {
    return contribuildApi.post('/register', data);
}

export const VERIFY_OTP = async (data: {
    otp : string,
}) => {
    return contribuildApi.post(`/verify-otp`, data);
}

export const CHECK_OTP = async (data: {
    otp: string,
    identifier?: string | null;
}) => {
    return contribuildApi.post(`/check-otp`, data);
}

export const RESEND_OTP = async () => {
    return contribuildApi.post('/resend-otp');
}

export const LOGIN_USER = async (data: {
    identifier: string,
    password: string
}) => {
    return contribuildApi.post('/login', data);
}

export const REQUEST_RESET = async (data: {
    identifier?: string | null,
}) => {
        return contribuildApi.post('/init-reset-password', data);
}

export const CREATE_PASSWORD = async (data: {
    identifier: string | null,
    password: string,
    confirm_password: string,
    otp: string | null,
}) => {
    return contribuildApi.post('/reset-password', data);

}