import React, { useState, type Dispatch, type SetStateAction } from 'react';
import { FilterLocation, FilterSelectWrap, FilterWrap, MobileMenuController, LeftCont} from '../../styles/reusable/header';
import RoleCardComp from './roleCard';
import { RandomCircle } from '../../styles/reusable/index';
import * as Icon from 'iconsax-react';
import FilterSelectCard from './filterPopupCard';

interface FilterCardProps {
    activeFilterType: string;
    setActiveFilterType: Dispatch<SetStateAction<string>>;
}

const FilterComp = ({activeFilterType, setActiveFilterType} : FilterCardProps) => {

    const [showSearch, setShowSearch] = useState(false);

    return(
        <>
            <FilterWrap>
                {
                    !showSearch ?
                    <FilterLocation>
                        <MobileMenuController>
                            <RandomCircle
                                size={'40px'}
                                onClick={() => setShowSearch(true)}
                            >
                                <Icon.SearchNormal1 size={18} />
                            </RandomCircle>
                        </MobileMenuController>
                        <div>
                            <p>Location:</p>
                            <RoleCardComp 
                                text='Lekki, Lagos'
                                hasIcon={true}
                            />
                        </div>
                    </FilterLocation>
                    : null
                }
                {
                    showSearch ?
                    <LeftCont
                        mobileDisplay={true}
                    >
                        <div>
                            <i>
                                <Icon.SearchNormal1 size={18} />
                            </i>
                            <i>
                                <Icon.ArrowCircleRight2 
                                    size={28} 
                                    variant='Bold' 
                                    color='var(--secondary-color)' 
                                    onClick={() => setShowSearch(false)}
                                />
                            </i>
                            <input 
                                placeholder='Search'
                            />
                        </div>
                    </LeftCont>
                    : null
                }
                <FilterSelectWrap>
                    <FilterSelectCard
                        filterType='Price'
                        activeFilterType={activeFilterType}
                        setActiveFilterType={setActiveFilterType}
                    />
                    <FilterSelectCard
                        filterType='Type'
                        activeFilterType={activeFilterType}
                        setActiveFilterType={setActiveFilterType}
                    />
                    <FilterSelectCard
                        filterType='Beds'
                        activeFilterType={activeFilterType}
                        setActiveFilterType={setActiveFilterType}
                    />
                    <FilterSelectCard
                        filterType='Baths'
                        activeFilterType={activeFilterType}
                        setActiveFilterType={setActiveFilterType}
                    />
                </FilterSelectWrap>
            </FilterWrap>
        </>
    )
}

export default FilterComp;