import React, { type Dispatch, type SetStateAction } from 'react';
import { CheckBoxCard, FilterPopupCard, FilterSelect } from '../../styles/reusable/filter';
import { ChevronDownIcon } from '@heroicons/react/24/outline';
import { Button } from '../../styles/reusable';
import { InputWrap, InputField } from '../../styles/authentication';
import Typography from './typography';
import { Fade } from 'react-awesome-reveal';

interface FilterCardProps {
    filterType: string;
    activeFilterType: string;
    setActiveFilterType: Dispatch<SetStateAction<string>>;
}

const FilterSelectCard = ({filterType, activeFilterType, setActiveFilterType} : FilterCardProps) => {
    return(
        <>
            <FilterSelect
                onClick={(e) => {
                    e.stopPropagation();
                    setActiveFilterType(filterType)}
                }
            >
                <p>{filterType}</p>
                <ChevronDownIcon className="w-4 h-4" color="#8796AD" />
                {
                    activeFilterType === filterType &&
                        <Fade duration={700}>
                            <FilterPopupCard>
                                {
                                    activeFilterType === 'Price' &&
                                    <>
                                        <InputWrap>
                                            <InputField
                                            >
                                                <p>Min Price</p>
                                                <input 
                                                    placeholder='₦0'
                                                    autoComplete="off"
                                                    type='number'
                                                />
                                            </InputField>
                                            <InputField
                                            >
                                                <p>Max Price</p>
                                                <input 
                                                    placeholder='₦0'
                                                    autoComplete="off"
                                                    type='number'
                                                />
                                            </InputField>
                                        </InputWrap>
                                        <Button
                                            bg='var(--primary-color)'
                                            color='#fff'
                                            type='button'
                                            width='100%'
                                        >
                                            Apply
                                        </Button>
                                    </>
                                }
                                {
                                    activeFilterType === 'Type' &&
                                    <>
                                        <Typography 
                                            text='Property Type'
                                            color='#1B2229'
                                            fontWeight={600}
                                            fontSize='14px'
                                            lineHeight='16.8px'
                                            margin="0 0 15px 0"
                                        />
                                        <InputWrap>
                                            {
                                                (apartmentTypes && apartmentTypes.length > 0) &&
                                                    <>
                                                        {
                                                            apartmentTypes.map((item, index) => (
                                                                <CheckBoxCard
                                                                    key={index}
                                                                >
                                                                    <input type="checkbox" name='apartment_type' />
                                                                    <p>{item}</p>
                                                                </CheckBoxCard>
                                                            ))
                                                        }
                                                    </>
                                            }
                                        </InputWrap>
                                        <Button
                                            bg='var(--primary-color)'
                                            color='#fff'
                                            type='button'
                                            width='100%'
                                        >
                                            Apply
                                        </Button>
                                    </>
                                }
                                {
                                    activeFilterType === 'Beds' &&
                                    <>
                                        <Typography 
                                            text='Beds'
                                            color='#1B2229'
                                            fontWeight={600}
                                            fontSize='14px'
                                            lineHeight='16.8px'
                                            margin="0 0 15px 0"
                                        />
                                        <InputWrap>
                                            {
                                                <>
                                                    {
                                                        [1, 2, 3, 4].map((item, index) => (
                                                            <CheckBoxCard
                                                                key={index}
                                                            >
                                                                <input type="checkbox" name='bed' />
                                                                <p>{item} {item === 1 ? "Bed" : "Beds"}</p>
                                                            </CheckBoxCard>
                                                        ))
                                                    }
                                                </>
                                            }
                                        </InputWrap>
                                        <Button
                                            bg='var(--primary-color)'
                                            color='#fff'
                                            type='button'
                                            width='100%'
                                        >
                                            Apply
                                        </Button>
                                    </>
                                }
                                {
                                    activeFilterType === 'Baths' &&
                                    <>
                                        <Typography 
                                            text='Baths'
                                            color='#1B2229'
                                            fontWeight={600}
                                            fontSize='14px'
                                            lineHeight='16.8px'
                                            margin="0 0 15px 0"
                                        />
                                        <InputWrap>
                                            {
                                                <>
                                                    {
                                                        [1, 2, 3, 4].map((item, index) => (
                                                            <CheckBoxCard
                                                                key={index}
                                                            >
                                                                <input type="checkbox" name='bath' />
                                                                <p>{item} {item === 1 ? "Bath" : "Baths"}</p>
                                                            </CheckBoxCard>
                                                        ))
                                                    }
                                                </>
                                            }
                                        </InputWrap>
                                        <Button
                                            bg='var(--primary-color)'
                                            color='#fff'
                                            type='button'
                                            width='100%'
                                        >
                                            Apply
                                        </Button>
                                    </>
                                }
                            </FilterPopupCard>
                        </Fade>
                }
            </FilterSelect>
        </>
    )
}

export default FilterSelectCard;

const apartmentTypes = [
    "Apartment",
    "Mansion",
    "Condo",
    "Flats"
]
