import React, { useEffect, useState } from 'react';
import { MainWrap,LoneWrap, PinInputWrap, LinkText } from '../../../styles/authentication';
import { Button } from '../../../styles/reusable';
import { useNavigate } from 'react-router';
import AuthHeaderComp from '../authHeader';
import OTPInput from 'react-otp-input';
import { RESEND_OTP, VERIFY_OTP, CHECK_OTP, REQUEST_RESET } from '../../../api/auth/onboarding';
import { useMutation } from '@tanstack/react-query';
import { Spinner } from '../../reusable/spinner';
import { enqueueSnackbar } from 'notistack';
import { useSearchParams } from 'react-router-dom';


const width = window.innerWidth;

const Verify = () => {
    const navigate = useNavigate();
    const [ PARAMS_SEARCH ] = useSearchParams();
    const type: string | null = PARAMS_SEARCH.get('type');
    const identifier: string | null = PARAMS_SEARCH.get('identifier');
    const [verificationCode, setVerificationCode] = useState('');
    const [timer, setTimer] = useState(120);

    // Async functions

    const handleVerifyFn = (type: string | null) => {
        switch (type) {
            case 'reset':
                return CHECK_OTP
            case 'onboard':
                return VERIFY_OTP
            default:
                break;
        }
    }

    const handleResendFn = (type: string | null) => {
        switch (type) {
            case 'reset':
                return REQUEST_RESET
            case 'onboard':
                return RESEND_OTP
            default:
                break;
        }
    }

    // For Verifying otp
    const { mutateAsync, isPending } = useMutation({
        mutationFn: handleVerifyFn(type),
        onSuccess: (data) => {
            if (type === 'reset'){
                navigate(`/create-password?otp=${verificationCode}&identifier=${identifier}`);
            }else {
                navigate('/dashboard');
            }
        }
    })

    // For Resending OTP
    const { mutateAsync: mutateResendOtp, isPending: resendprocess } = useMutation({
        mutationFn: handleResendFn(type),
        onSuccess: () => {
            enqueueSnackbar({
                variant: 'success',
                message: 'OTP has been resent to your phone number and email.'
            });
            setTimer(120);
        }
    })

    // Function to call mutate function
    const verifyOtp = (e: any) => {
        e.preventDefault();
        if (verificationCode && verificationCode.length === 4){
            if (type === 'reset'){
                mutateAsync({
                    otp: verificationCode,
                    identifier,
                })
            }else {
                mutateAsync({
                    otp: verificationCode,
                })
            }
        }else {
            enqueueSnackbar({
                variant: 'error',
                message: 'OTP must be four characters!'
            })
        }
    }

    // Resend OTP mutate
    const resendOtp = () => {
        if (timer === 0 || timer < 0 ){
            mutateResendOtp({
                identifier
            });
        }
    }

    // Timer functions
    useEffect(() => {
        if (timer > 0 && timer !== 0){
            setInterval(() => {
                if (timer > 0){
                    setTimer(timer => timer - 1);
                }
            }, 1000)
        }
    }, []) //eslint-disable-line

    return(
        <>
            <MainWrap>
                <AuthHeaderComp />
                <LoneWrap>
                    <h3>
                        Verify phone number
                    </h3>
                    <p>Enter 5-digit code sent to your phone number</p>
                    <PinInputWrap>
                        <OTPInput
                        value={verificationCode}
                        onChange={(code:string) => setVerificationCode(code)}
                        numInputs={4}
                        inputStyle={inputStyle}
                        isInputNum={true}
                        shouldAutoFocus={true}
                        isInputSecure={false}
                        placeholder='----'
                        />
                    </PinInputWrap>
                    {
                        timer > 0 &&
                            <LinkText
                                color={'#245372'}
                                fontSize='14px'
                                align='center'
                                margin='3rem 0'
                            >
                                Resend in <span style={{textDecoration: "underline", color:'#000'}}><b>{timer}s</b></span>
                            </LinkText>
                    }
                    {
                        (timer === 0 || timer < 0) &&
                            <Button
                                bg='#fff'
                                color='var(--secondary-color)'
                                type='button'
                                width='100%'
                                top='30px'
                                onClick={() => resendOtp()}
                                disabled={timer > 0 && resendprocess}
                            >
                                {resendprocess ? <Spinner className='text-[#147EFA]' /> : 'Resend Code'}
                            </Button>
                    }
                    {resendprocess &&
                        <Button
                            bg='#fff'
                            color='#147EFA'
                            type='button'
                            width='100%'
                            top='0px'
                            disabled={timer > 0 && resendprocess}
                        >
                            <Spinner className='text-[var(--primary-color)]' />
                        </Button>
                    }
                    <Button
                        bg='var(--primary-color)'
                        color='#fff'
                        type='submit'
                        width='100%'
                        top='0px'
                        onClick={(e) => verifyOtp(e)}
                        disabled={isPending}
                    >
                        {isPending ? <Spinner /> : "Verify Phone"}
                    </Button>
                </LoneWrap>
            </MainWrap>
        </>
    )
}

export default Verify;

export const inputStyle = {
    width: width > 728 ? '56px' : '56px',
    height: width > 728 ? '56px' : '56px',
    background: '#fff',
    border: '1px solid #F0F3F6',
    borderRadius: '4px',
    textAlign: 'center',
    fontWeight: 'bold',
    marginRight: 7.5,
    marginLeft: 7.5,
    color: 'black',
  };