import React, { useState } from 'react';
import Header from '../reusable/header';
import FilterComp from '../reusable/filter';
import { MainWrap, PropertyWrap } from '../../styles/reusable/index';
import Typography from '../reusable/typography';
import PropertyCard from '../reusable/propertyCard';
import PaginationComp from '../reusable/pagination';
import FooterComp from '../reusable/footer';
import { Link } from 'react-router-dom';
import { Button } from '../../styles/reusable';
import { SaveCard } from '../../pages/landing/style';
import MakeRequest from '../../pages/search/request';

const SearchIndex = () => {

    const [activeFilterType, setActiveFilterType] = useState('');
    const [openRequest, setOpenRequest] = useState(false);

    return(
        <>
            <div
                onClick={() => setActiveFilterType('')}
            >
                <Header isSearchPage={true} />
                <FilterComp activeFilterType={activeFilterType} setActiveFilterType={setActiveFilterType} />
                <MainWrap>
                    <Typography 
                        text='About 100 places in Lekki, Lagos'
                        color='#000'
                        fontWeight={600}
                        fontSize='18px'
                        lineHeight='21.6px'
                    />
                    <PropertyWrap>
                        {
                            properties.map((item, index) => (
                                <Link key={index} to={`/property/itemName`}>
                                    <PropertyCard 
                                        key={index}
                                        img={item.img}
                                        saved={item && item.saved ? item.saved : false}
                                    />
                                </Link>
                            ))
                        }
                    </PropertyWrap>
                    <PaginationComp />
                    <div className='mt-[7rem]'>
                        <SaveCard>
                            <div>
                                <Typography 
                                    text={'Can’t find a property you like?'}
                                    color='#1B2229'
                                    fontWeight={600}
                                    fontSize='32px'
                                    lineHeight='32px'
                                />
                                <Typography 
                                    text={'Tell us more about it and we’d get in touch with you as soon as we can.'}
                                    color='#1B2229'
                                    fontWeight={400}
                                    fontSize='16px'
                                    lineHeight='22.4px'
                                    margin='1rem 0 0rem 0'
                                />
                                <Button
                                    bg='var(--primary-color)'
                                    color='#fff'
                                    type='button'
                                    width='auto'
                                    onClick={() => setOpenRequest(true)}
                                >
                                    Tell us about it
                                </Button>
                            </div>
                            <img 
                                src={'./images/chat.png'}
                                alt={'Building'}
                            />
                        </SaveCard>
                    </div>
                </MainWrap>
                <FooterComp />
                {
                    openRequest && (
                        <MakeRequest
                            closeFunc={() => setOpenRequest(false)}
                        />
                    )
                }
            </div>
        </>
    )
}

export default SearchIndex;

